import React from 'react'
import { Divider } from 'antd'
import './studentContent.css'
import { withRouter } from 'react-router-dom'
import StudentCard from 'components/detail/student/student-card/StudentCard'
import Introduction from './introduction/Introduction'
import { DetailOptions } from '../common'
import { MobileAIApi } from 'src/ajax'
import { observer, inject } from 'mobx-react'

@withRouter
@inject('userStore')
@observer
export default class StudentContent extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      userType: 0,
      isValid: true,
      QQ: '',
      Wechat: '',
      realName: '',
      gender: '0',
      // location: null,
      birthday: 1449072000000,
      stuLevel: '',
      school: '',
      department: '',
      major: '',
      grade: 2,
      skill: 'JAVA',
      photo: 'http://ufind-10067447.cossh.myqcloud.com/userAvatar/defaultAvatar.svg',
      isRealName: true,
      type: '1',
      introduction: '',
      educations: []
    }
  }

  componentWillReceiveProps(nextProps) {
    this.getStuDetail(nextProps.id)
  }

  componentWillMount() {
    this.getStuDetail(this.props.id)
  }

  async getStuDetail(sid) {
    try {
      const body = {id: sid}
      const { data } = await MobileAIApi.fetchStudentDetail(body)
      this.setState({
        realName: data.name,
        QQ: data.QQ,
        Wechat: data.Wechat,
        gender: data.gender,
        school: data.school,
        major: data.major,
        grade: data.year,
        department: data.department,
        introduction: data.introduction,
        skill: data.skill,
        photo: data.avatar,
        birthday: data.birthday,
        title: data.title
      })
    } catch (e) {
      console.log(e)
    }
  }

  render() {
    const intro = (
      <div>
        {/* <Divider><span styleName="divider">TA</span></Divider> */}
        {/* <ProjectsJoin /> */}
        <Divider><span styleName="divider"></span></Divider>
        <Introduction introduction={this.state.introduction} />
        <Divider><span styleName="divider"></span></Divider>
        <Introduction introduction={this.state.skill} />
      </div>
    )
    return (
      <div>
        <StudentCard stuInfo={this.state} />
        <DetailOptions type="student" intro={intro} />
      </div>
    )
  }
}



