import React from 'react'
import './transferInfo.css'

export default class TransferInfo extends React.Component {
  priceDigitUpper(n) {
    n = n * 10000
    const fraction = ['', '']
    const digit = [
      '', '', '', '', '',
      '', '', '', '', ''
    ]
    const unit = [
      ['', '', ''],
      ['', '', '', '']
    ]
    const head = n < 0 ? '' : ''
    n = Math.abs(n)
    let s = ''
    for (let i = 0; i < fraction.length; i++) {
      s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '')
    }
    s = s || ''
    n = Math.floor(n)
    for (let i = 0; i < unit[0].length && n > 0; i++) {
      let p = ''
      for (let j = 0; j < unit[1].length && n > 0; j++) {
        p = digit[n % 10] + unit[1][j] + p
        n = Math.floor(n / 10)
      }
      s = p.replace(/(.)*$/, '').replace(/^$/, '') + unit[0][i] + s
    }
    return head + s.replace(/(.)*/, '')
      .replace(/(.)+/g, '')
      .replace(/^$/, '')
  }

  render() {
    const info = this.props.info
    return (
      <div>
        <ul>
          <li styleName="info-item">{info.patentName}</li>
          <li styleName="info-item">{info.applicationNumber}</li>
          <li styleName="info-item">{info.inventor}</li>
          <li styleName="info-item">{info.money} ({this.priceDigitUpper(info.money)})</li>
        </ul>
      </div>
    )
  }
}



